:local(.searchContainer) {
  margin-top: 24px; }

:local(.results) {
  border: 1px solid var(--spectrum-global-color-gray-400);
  position: absolute;
  background-color: var(--spectrum-global-color-gray-50);
  padding-top: 3px;
  padding-bottom: 3px;
  z-index: 2000;
  width: 368px;
  border-radius: 4px;
  margin-top: 5px;
  display: none;
  max-height: calc(100vh - 200px);
  min-height: 220px;
  overflow-y: auto;
  box-shadow: 0 1px 4px var(--spectrum-alias-dropshadow-color); }

:local(.overlay) {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  opacity: 0.4;
  z-index: 1999;
  display: none; }
  :local(.overlay):local(.overlayOpen) {
    display: block; }

:local(.resultSet) {
  list-style: none;
  padding-left: 0px;
  color: var(--spectrum-global-color-gray-800) !important;
  margin: 0px; }

:local(.resultContainer):not(:last-of-type):after {
  content: '';
  display: block;
  margin: 0 12px;
  margin-top: 4px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--spectrum-global-color-gray-200); }

:local(.listItem) {
  display: block;
  padding: 6px 12px 6px 10px;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  cursor: pointer;
  text-decoration: none;
  border-left: 2px solid transparent; }
  :local(.listItem):hover {
    transition: all 125ms ease-in-out;
    background-color: var(--spectrum-alias-highlight-hover); }
  :local(.listItem).is-highlighted, :local(.listItem):focus {
    background-color: var(--spectrum-alias-highlight-hover); }
  :local(.listItem):focus {
    outline: none;
    border-left-color: var(--spectrum-global-color-blue-400); }

:local(.open) {
  display: block; }

:local(.subHeader) {
  color: var(--spectrum-global-color-gray-700) !important;
  margin: 0px; }

:local(.header) {
  height: 28px;
  display: flex;
  align-items: center;
  padding-left: 12px;
  padding-right: 12px; }

:local(.file) {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start; }

:local(.icon) {
  height: 16px;
  margin-right: 8px; }
  :local(.icon) img {
    width: 16px;
    height: 16px; }

:local(.noResultsContainer) {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 64px;
  margin-top: 64px; }

:local(.noResults) {
  color: var(--spectrum-global-color-gray-700) !important; }

:local(.noResultsSub) {
  color: var(--spectrum-global-color-gray-700) !important; }
