:local(.card) {
  width: 224px;
  margin: 2px 16px 2px 2px;
  box-shadow: 0 0 0 1px var(--spectrum-global-color-gray-300);
  transition: 125ms ease-in-out;
  height: 64px;
  display: flex;
  flex-direction: row;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer; }
  :local(.card):hover {
    box-shadow: 0 0 0 1px var(--spectrum-global-color-gray-400);
    transition: 125ms ease-in-out; }
  @media screen and (max-width: 960px) {
    :local(.card) {
      margin-bottom: 16px; } }
  :local(.card) img, :local(.card) svg {
    width: 64px;
    height: 64px; }
  :local(.card) .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 16px; }
  :local(.card) .cardTitle {
    font-weight: 700 !important;
    margin-bottom: 0 !important;
    margin-top: 0 !important; }
  :local(.card) .cardSubTitle {
    color: var(--spectrum-global-color-gray-700) !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important; }

:local(.link) {
  text-decoration: none; }

:local(.row) {
  margin-bottom: 56px; }

:local(.link) {
  text-decoration: none; }

:local(.cardButton) {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: none;
  outline-style: none;
  display: inline-block;
  text-decoration: none; }
  :local(.cardButton) > div:focus {
    outline: none;
    outline-style: none; }
  :local(.cardButton):focus > div {
    box-shadow: 0 0 0 2px #2680eb;
    outline: none; }

:local(.npmImage) {
  background: rgba(203, 56, 55, 0.1); }

:local(.githubImage) {
  background-color: var(--spectrum-global-color-gray-200);
  fill: black; }

:local(.spectrumImage) {
  background-color: var(--spectrum-global-color-gray-100); }

:global(.spectrum--dark) :local(.githubImage),
:global(.spectrum--darkest) :local(.githubImage) {
  background-color: rgba(245, 245, 245, 0.1);
  fill: whitesmoke; }
