:local(.appHeader) {
  height: 48px;
  border-bottom: 1px solid var(--spectrum-global-color-gray-200);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  background-color: var(--spectrum-global-color-gray-50);
  top: 0px;
  right: 0px;
  left: 0px;
  padding-left: 8px;
  z-index: 10; }
  @media screen and (min-width: 961px) {
    :local(.appHeader) {
      display: none; } }

:local(.overlay) {
  display: none;
  visibility: hidden;
  background-color: var(--spectrum-alias-background-color-modal-overlay);
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 11; }
  @media screen and (max-width: 960px) {
    :local(.overlay) {
      display: block;
      /* Exit animations */
      transition: opacity var(--spectrum-global-animation-duration-200) ease-out 0ms, visibility 0ms linear var(--spectrum-global-animation-duration-200); }
      :local(.overlay):local(.menuOpen) {
        visibility: visible;
        opacity: 1;
        pointer-events: auto;
        /* Entry animations */
        transition: opacity var(--spectrum-global-animation-duration-200) ease-in 0ms; } }

:local(.sideBar) {
  z-index: 12;
  position: fixed;
  left: 0px;
  top: 0px;
  bottom: 0px;
  width: 248px;
  transition: none; }
  @media screen and (max-width: 960px) {
    :local(.sideBar) {
      position: fixed;
      top: 0;
      bottom: 0;
      left: auto;
      right: 100%;
      z-index: 100;
      transform: translateX(0);
      transition: transform var(--spectrum-global-animation-duration-200) ease-in-out; }
      :local(.sideBar):local(.menuOpen) {
        transform: translateX(100%); } }

:local(.spectrumSidebar) {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--spectrum-global-color-gray-75); }

:local(.header) {
  padding: 30px 24px 24px 24px;
  width: 248px; }

:local(.navigation) {
  padding: 0px 24px;
  flex: 1;
  flex-grow: 1;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto; }

:local(.logo) {
  width: 30px;
  display: block;
  margin-right: 16px; }

:local(.title) {
  display: flex;
  flex-direction: row; }

:local(.headerLink) {
  text-decoration-line: none !important; }

:local(.titleText) {
  margin: 0px !important; }

:local(.internal) {
  font-size: 12px;
  margin: 0px;
  color: #8E8E8E;
  font-weight: 600; }
